  text-decoration: none;
}

a:active, a:focus {
  border: none;
  outline: none;
}

button, figure {
  color: inherit;
  border: none;
  outline: none;
}

button:focus, button:active, figure:focus, figure:active {
  outline: none;
  border: none;
}

select:focus, select:active {
  outline: none;
}

header, section, footer {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.wrapper {
