.button {
  display: inline-block;
  width: 100%;
  color: #fff;
  background-color: #404040;
  padding: 13px 45px;
  font-size: 14px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  font-weight: 400;
  border-radius: 2px;
  text-align: center;
  position: relative;
  transition: 0.25s ease;
  cursor: pointer;
  border: none;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
}

.button:hover {
  background-color: #333333;
}

.button:active {
  background-color: #2b2b2b;
}

.btn-scroll {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.btn-scroll:after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 180%;
  height: 180%;
  content: '';
}

@keyframes move-arrow {
  0% {
    transform: translate3d(0, -7px, 0);
    opacity: 1;
  }
  15% {
    opacity: 1;
  }
  100% {
    transform: translate3d(0, 8px, 0);
    opacity: 0;
  }
}

