.menu-button {
  display: none;
  opacity: 0;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 15px;
  top: 15px;
  width: 55px;
  height: 55px;
  padding: 5px;
  border-radius: 50%;
  background-color: #e7746f;
  cursor: pointer;
  z-index: 8;
  transition: 0.25s ease;
}

@media (max-width: 767.98px) {
  .menu-button {
    display: flex;
    opacity: 1;
  }
}

.menu-button span {
  display: block;
  position: absolute;
  width: 23px;
  height: 2px;
  background-color: #fafafa;
  transition: 0.25s ease;
}

.menu-button span:before, .menu-button span::after {
  display: block;
  position: absolute;
  width: 23px;
  height: 2px;
  background-color: #fafafa;
}

.menu-button span:before {
  content: '';
  top: -7px;
}

.menu-button span:after {
  content: '';
  top: 7px;
}

.menu-button--show span {
  transform: rotate(45deg);
}

.menu-button--show span:before {
  top: 0;
  transform: rotate(0);
}

.menu-button--show span:after {
  top: 0;
  transform: rotate(90deg);
}

.page-header {
  display: flex;
  justify-content: center;
  align-content: center;
  background-attachment: fixed;
  width: 100vw;
  max-width: 100%;
