  text-transform: uppercase;
  font-weight: 800;
  padding: 22px 0;
  margin-bottom: 0;
}

.page-header-composition:before, .page-header-composition:after {
  position: absolute;
  display: block;
  content: '';
  width: 30%;
  height: 30%;
}

