.page-header-composition:before {
  top: 0;
  left: 0;
  border-top: 6px solid rgba(255, 255, 255, 0.75);
  border-left: 6px solid rgba(255, 255, 255, 0.75);
  transform: translate(-45px, -50px);
  border-top-left-radius: 2px;
