  bottom: 0;
  right: 0;
  border-bottom: 6px solid rgba(255, 255, 255, 0.75);
  border-right: 6px solid rgba(255, 255, 255, 0.75);
  transform: translate(45px, 50px);
  border-bottom-right-radius: 2px;
