.s-callback__mail-address {
  display: inline-block;
}

.s-callback-form {
  margin-top: 55px;
}

.s-callback-form__field {
  display: block;
  width: 100%;
  padding: 12px 15px;
  border-radius: 2px;
  margin-bottom: 15px;
  border: 1px solid #d0d0d0;
}

.s-callback-form__select {
  padding: 0;
  border: none;
}

.s-callback-form__select .selectize-dropdown,
.s-callback-form__select .selectize-input,
.s-callback-form__select .selectize-input input {
  font-size: inherit;
  line-height: 1.2;
  color: #333333;
}

.s-callback-form__select .selectize-dropdown .option,
.s-callback-form__select .selectize-input .option,
.s-callback-form__select .selectize-input input .option {
  padding: 12px 15px;
  cursor: pointer;
  opacity: .8;
}

.s-callback-form__select .selectize-dropdown .option:hover,
.s-callback-form__select .selectize-input .option:hover,
.s-callback-form__select .selectize-input input .option:hover {
  background-color: #fafafa;
  opacity: 1;
}

.s-callback-form__select .selectize-input {
  padding: 12px 15px;
  border-radius: 2px;
  box-shadow: none;
}

.s-callback-form__select .selectize-dropdown {
  border-radius: 2px;
  border: 1px solid #d0d0d0;
}

.s-callback-form__select .selectize-dropdown .active {
  color: #333333;
}

.s-callback-form__success {
  position: fixed;
  top: 48px;
  left: 50%;
  transform: translate(-50%, -120%);
  display: block;
  width: 500px;
  margin: 0 auto 0;
  text-align: center;
  font-family: "Raleway", "Arial", sans-serif;
  background-color: #fcfcfc;
  box-shadow: 0 2px 3px 1px rgba(0, 0, 0, 0.25);
  font-size: 18px;
  transition: .8s ease;
  opacity: 0;
  z-index: -1;
  user-select: none;
}

.s-callback-form__success p {
  padding: 26px 15px;
  margin-bottom: 0;
}

.s-callback-form__success p span {
  color: #e7746f;
}

.s-callback-form__success--visible {
  transform: translate(-50%, 0);
  user-select: initial;
  z-index: 3;
  opacity: 1;
}
